@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif;
  @apply bg-backgroundColor text-default;
}

.button-sm {
  @apply text-default border-default rounded-small min-w-16 hover:text-hoverText hover:bg-hoverBackground h-8 gap-2 px-3 text-sm;
}

.button-md {
  @apply text-default border-default min-w-20 rounded-medium hover:text-hoverText hover:bg-hoverBackground h-10 px-4 text-base;
}

.button-lg {
  @apply text-default border-default min-w-24 rounded-large hover:text-hoverText hover:border-hoverText hover:bg-hoverBackground h-12 gap-3 px-6 text-lg;
}

.button-icon {
  @apply text-default border-default min-w-10 min-h-10 disabled:hover:text-default hover:text-hoverText hover:bg-hoverBackground h-10 w-10 disabled:cursor-not-allowed disabled:opacity-30 disabled:hover:bg-transparent;
}

.element-icon {
  @apply text-selectedText border-selectedText min-w-10 min-h-10 hover:bg-elementHoverBackground hover:text-elementHoverText hover:border-elementHoverBackground h-10 w-10;
}

@layer utilities {
  .text-4xl {
    font-size: 2.25rem;
    line-height: 2.5rem;
  }
  .text-3xl {
    font-size: 1.875rem;
    line-height: 2.25rem;
  }
  .text-2xl {
    font-size: 1.5rem;
    line-height: 2rem;
  }
  .text-xl {
    font-size: 1.25rem;
    line-height: 1.75rem;
  }
  .text-lg {
    font-size: 1.125rem;
    line-height: 1.75rem;
  }
  .text-base {
    font-size: 1rem;
    line-height: 1.5rem;
  }
  .text-sm {
    font-size: 0.875rem;
    line-height: 1.25rem;
  }
  .text-xs {
    font-size: 0.75rem;
    line-height: 1rem;
  }
  .text-2xs {
    font-size: 0.625rem;
    line-height: 0.875rem;
  }
}
